.video{
    position: relative;
    max-height: 80rem;
    overflow: hidden;
    .overlay{
        z-index: 2;
        position: absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,1+100 */
        background: -moz-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%, rgba(158,55,242,0.93) 54%, rgba(255,255,255,1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.93) 52%,rgba(255,255,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.93) 67%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }

    video{
        display: block;
        z-index: 1;
        // position: absolute;
        width: 100%;
        height: 100%;
    }
}
.contenido-video{
    display: flex;     
    align-items: center;
    height: 100%;
    h2{
        display: flex;
        flex-direction: column;
        color: $blanco;
        align-items: flex-start;
        font-size: 7rem;
        z-index: 1;
        span{
            font-size: 2rem,;
            background-color: $rosa;
            padding: .8rem;
            font-weight: $gruesa;
        }
    }
}
