html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.scroll-body {
    padding-top: 10.6rem; }

p {
  font-size: 2rem;
  color: #000; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0; }

h1 {
  font-size: 4rem;
  font-weight: 800; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.datosAnuncio {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  justify-items: center;
  padding: 5rem 0; }

.informacionA h2 {
  display: flex;
  flex-direction: column;
  padding: 3rem; }
  .informacionA h2 span {
    margin-top: 2rem;
    font-size: 2rem;
    color: #f53756; }
  .informacionA h2 p {
    font-weight: 500;
    margin-top: 3.5rem; }

.header {
  background-color: #4cd8b3; }
  .header .contenido-header {
    padding: calc( 5rem - 2rem) 0; }
    @media (min-width: 768px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: white;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin: 0; } }
  .header.header-static {
    left: 0;
    top: 0;
    width: 100%;
    position: fixed; }

.navegacion {
  display: none; }
  @media (min-width: 480px) {
    .navegacion {
      display: inline;
      display: flex;
      gap: 2rem;
      justify-content: center; } }
  .navegacion a {
    color: white;
    font-size: 2rem; }
    .navegacion a:hover {
      color: #fef460; }

.video {
  position: relative;
  max-height: 80rem;
  overflow: hidden; }
  .video .overlay {
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,1+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.93) 54%, white 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.93) 52%, white 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.93) 67%, white 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    z-index: 1;
    width: 100%;
    height: 100%; }

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%; }
  .contenido-video h2 {
    display: flex;
    flex-direction: column;
    color: white;
    align-items: flex-start;
    font-size: 7rem;
    z-index: 1; }
    .contenido-video h2 span {
      font-size: 2rem;
      background-color: #f53756;
      padding: .8rem;
      font-weight: 700; }

.line-up {
  background: #f53756;
  padding: calc( 5rem - 2rem) 0; }
  .line-up h3, .line-up p {
    color: white;
    text-align: center;
    margin: calc( 5rem - 2rem); }
  .line-up li {
    padding: 2.5rem 0 2.5rem 2.5rem;
    margin-left: 0; }
  .line-up .liO {
    background-color: #0E6B52; }

.lineUpc {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem; }
  .lineUpc .nombre-escenario {
    color: #000;
    font-size: 2rem;
    font-weight: 500; }
    .lineUpc .nombre-escenario:after {
      content: '';
      width: 10rem;
      height: 10rem;
      display: block;
      background-size: 15rem;
      background-repeat: no-repeat;
      margin: 2rem auto;
      background-position: center; }

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png); }

.EDM .nombre-escenario:after {
  background-image: url(../img/icono_edm.png); }

.calendario {
  padding-left: 0;
  list-style: none; }
  .calendario span {
    font-weight: 700; }

.bg-verde .calendario li:nth-child(odd) {
  background-color: #2bc69d; }

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fef02d; }

.bg-amarillo {
  background-color: #fef460; }

.bg-verde {
  background-color: #4cd8b3; }

.boletos {
  background-color: #4cd8b3;
  padding: 7rem 0 8rem 0; }
  .boletos h3 {
    margin-bottom: 4rem;
    text-align: center;
    color: white; }
  .boletos .datos-boletos {
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 2rem; }

.informacion-boleto {
  list-style: none; }
  .informacion-boleto li {
    padding: 1rem;
    background-color: white; }
    .informacion-boleto li span {
      font-weight: 600; }
    .informacion-boleto li:after {
      background-color: #000; }
  .informacion-boleto .rosa {
    background-color: #f53756;
    padding: 1.5rem;
    text-align: center;
    color: white; }
  .informacion-boleto .morado {
    background-color: #752f97;
    padding: 1.5rem;
    text-align: center;
    color: white; }

.picture h3 {
  padding: calc( 5rem - 2rem);
  text-align: center; }

.picture li {
  list-style: none; }

.galeriaI {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: calc( 5rem - 2rem);
  padding: calc( 5rem - 2rem); }
  @media (min-width: 768px) {
    .galeriaI {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: calc( 5rem - 2rem)/2; } }
  @media (min-width: 1200px) {
    .galeriaI {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      column-gap: calc( 5rem - 2rem); } }

.overlay {
  position: fixed;
  z-index: 2;
  top: 0;
  background-color: rgba(0, 0, 0, 0.9);
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden; }

.bt-cerrar {
  background-color: white;
  padding: 1.5rem;
  border-radius: 50%;
  font-weight: 900; }
  .bt-cerrar:hover {
    cursor: pointer;
    background-color: red;
    color: white; }

footer {
  padding: 1rem;
  background-color: #f53756; }
  footer p {
    color: white;
    text-align: center; }

.up {
  visibility: hidden;
  position: fixed;
  bottom: 4rem;
  left: 95%; }
  .up a {
    background-color: #f7f7f7;
    border-radius: 50%;
    padding: 2rem; }
    .up a:hover {
      color: #00000a;
      background-color: #752f97;
      border-radius: 50%; }

.ocultarBoton {
  visibility: visible; }
